import RegionShow from "@/components/RegionShow";
import FooterShow from "@/components/FooterShow";
import { Head } from '@vueuse/head';
import update from '@/mixins/update';
let url = process.env.NODE_ENV === 'development' // ? 'https://covid-api.caduo.ml/'
? 'http://localhost/ccr-api/' : 'https://covid-api.caduo.ml/';
export default {
  name: "HomeView",
  components: {
    RegionShow,
    FooterShow,
    Head
  },
  computed: {
    api_url() {
      let file_name = this.$route.params.fileName;

      if (file_name) {
        return url + file_name + '.json';
      } else {
        return url + 'latest.json';
      }
    },

    type_latest() {
      return !this.$route.params.fileName;
    }

  },

  data() {
    return {
      title: "全国疫情中高风险地区查询",
      info_url: url + 'info.json'
    };
  },

  mixins: [update]
};